import React, { useState } from 'react'
import you from '../imgs/youtube.png';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import './style.css';
import TextField from '@material-ui/core/TextField';

import getDownloadUrl from 'facebook-video-downloader';
function Youtube() {
    const [link, setlink] = useState('');
    const [show, setshow] = useState(false);
    const [url, seturl] = useState('');

    const send = () => {
        if (link === '') {
            alert('Please Enter Valid URL')
        }
        else{

            getDownloadUrl(link).then(respo => {
                seturl(respo.hd.url);
setshow(true);

const aa = document.createElement('a');
aa.setAttribute('href',respo.hd.url);
aa.setAttribute('download',"download");
aa.style.display = "none";
aa.click();
aa.remove();
                }).catch(() => {
                    alert('Can not find video :(');
                })

        }
    }
    return (
        <>
            <div className="mainbox">
                <div className="imgbx">
                    <img src={you} alt="picture" />
                </div>
                <div className="btnbox">
                    <TextField id="outlined-basic" value={link} onChange={(e) => { setlink(e.target.value) }} label="Enter video URL" variant="outlined" />

                    <button className="btns" onClick={send}>
                        <ArrowDownwardIcon /><span> Get Video</span></button>
                </div>
                
            </div>
        </>
    )
}

export default Youtube













