import React,{useState} from 'react';
import ins from '../imgs/insta.png';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import './style.css';
import TextField from '@material-ui/core/TextField';
import cheerio from 'cheerio';
import request from 'request';
function Instagram() {

    const [link, setlink] = useState('');
    const [isres, setisres] = useState(false);
    const [url, seturl] = useState('');

    const send = () => {
        if(link === '') {
            alert('Please Enter Valid URL')
        }
        else{

            let video_url = link;
    console.log(video_url);
    if(video_url !== undefined){

        if(video_url.substring(0,8) === 'https://' || video_url.substring(0,7) === 'http://' 
                || video_url.substring(0,21) === 'https://www.instagram' || video_url.substring(0,20) === 'http://www.instagram.com'){

            request(video_url, (error, response, html) => {
                if(!error){
                    console.log('Insta_grab : '+video_url+' : Loaded');
                    let $ = cheerio.load(html);

                    let video_link = $('meta[property="og:video"]').attr('content');
                   
                    let url = $('meta[property="og:url"]').attr('content');
                
                    seturl(video_link);
                    setisres(true);
                    if(video_link) {
                        const aa = document.createElement('a');
                        aa.setAttribute('href',video_link);
                        aa.setAttribute('download',"download");
                        aa.style.display = "none";
                        aa.click();
                        aa.remove();
                    }
             
                    
                }else{
                    alert('Error, Unable to load webpage')
                }
            });
        }else{
            alert('Invalid URL')
        }

    }

        }
    }
    return (
        <>
<div className="mainbox">
     <div className="imgbx">
         <img src={ins} alt="picture" />
     </div>
     <div className="btnbox">
     <TextField id="outlined-basic" value={link} onChange={(e) => {setlink(e.target.value)}} label="Enter video URL" variant="outlined" />

         <button className="btns" onClick={send}>
         <ArrowDownwardIcon /><span> Get Video</span></button>
     </div>

</div>
        </>
    )
}

export default Instagram;































